/*Header*/
.header {
  background: rgb(41, 41, 41);
}

.header-title {
  font-size: 40px;
  padding-top: 20px;
  text-align: center;
  color: rgb(193, 252, 252);
}

.header-nav {
  display: flex;
  width: 20%;
  min-width: 380px;
  justify-content: space-between;
  margin-left: 20px;
  padding-bottom: 20px;
}

.header-link {
  font-size: 30px;
  text-decoration: none;
  color: white;
}
/*Homepage*/
.main-container {
  border: 1px solid rgb(233, 247, 248);
  height: 143vh;
  background: rgb(233, 247, 248);
}

.homepage-title,
.about-title,
.forms-title {
  font-size: 36px;
  text-align: center;
  color: rgb(14, 89, 119);
  padding-top: 20px;
}
/*About us page*/
.about-content {
  padding-left: 20px;
  font-size: 24px;
}
/* Cards */
.cards-container {
  margin-top: 20px;
  width: 100%;
  height: 88vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.card {
  width: 250px;
  height: 194px;
  margin: 20px;
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  transition: all 0.4s;
  cursor: pointer;
  overflow-y: hidden;
}

.card-container {
  overflow-y: hidden;
}

.card:hover {
  transform: scale(1.1);
  transition: all 0.4s;
}

.card-title {
  max-width: 250px;
  text-align: center;
  font-size: 14px;
  color: rgb(14, 89, 119);
  margin: 0 0 10px 0;
  overflow-x: hidden;
}

.card-price {
  font-size: 23px;
  position: relative;
  top: -65px;
  left: 31px;
  color: red;
}

.card-color {
  width: 40px;
  font-size: 15px;
  position: relative;
  top: -282px;
  left: 98px;
}

.card-year {
  width: 40px;
  font-size: 20px;
  position: relative;
  top: -180px;
  left: 180px;
}

.card-img {
  width: 100%;
  box-shadow: 0px 2px 13px grey;
}

/*Search*/
.search {
  margin: auto;
  height: 50px;
  width: 90%;
  display: flex;
  justify-content: space-around;
}

.page-info {
  color: rgb(14, 89, 119);
}

.prev-btn,
.next-btn {
  background: rgb(190, 226, 255);
  min-width: 70px;
  height: 30px;
  border-radius: 30px;
  font-size: 20px;
  cursor: pointer;
}

.prev-btn:hover,
.next-btn:hover,
.sort-btn:hover,
.page-count:hover {
  transform: scale(1.1);
  transition: all 0.4s;
}

.sort-btn {
  background: rgb(190, 226, 255);
  width: 80px;
  margin-left: 20px;
  margin-right: 20px;
  border-radius: 20px;
  cursor: pointer;
}

.page-count {
  border-radius: 50%;
  background: rgb(190, 226, 255);
  min-width: 40px;
  height: 40px;
  font-size: 24px;
  margin-right: 20px;
  cursor: pointer;
}

.search-form {
  display: flex;
  justify-content: center;
  margin: auto;
}

.search-text {
  position: absolute;
  top: 235px;
  left: 45px;
  font-size: 20px;
  max-width: 300px;
  color: rgb(14, 89, 119);
}

.search-btn {
  width: 100px;
  font-size: 20px;
  margin-left: 20px;
  border-radius: 15px;
  border: 1px solid rgb(14, 89, 119);
  color: rgb(14, 89, 119);
  transition: all 0.4s;
  cursor: pointer;
}

.search-btn:hover {
  transform: scale(1.1);
  transition: all 0.4s;
}

.search-input {
  height: 23px;
  width: 250px;
  border: 1px solid rgb(14, 89, 119);
}

.search-info {
  height: 100px;
  min-width: 200px;
  display: flex;
  font-size: 20px;
  flex-direction: column;
}

.page-show {
  text-align: center;
  width: 17px;
  border: 2px solid rgb(14, 89, 119);
  border-radius: 50%;
}
/*Not found page(404)*/
.not-found-page-title {
  font-size: 40px;
  text-align: center;
}

.link-not-found {
  font-size: 30px;
  margin-left: 100px;
}

.not-found-404 {
  margin: 0;
  font-size: 160px;
  text-align: center;
}
/*Forms page*/
.form {
  margin: auto;
  height: 100%;
  min-height: 485px;
  width: 90%;
  min-width: 440px;
  display: flex;
  justify-content: space-around;
  padding: 50px;
  background: rgb(190, 226, 255);
}

.form-column {
  display: flex;
  flex-direction: column;
}

.form-label {
  width: 85%;
  min-width: 280px;
  margin-bottom: 20px;
  padding: 25px;
  border-radius: 5px;
  box-shadow: 2px 2px 13px grey;
}

.title-label {
  margin: 0;
}

.errors {
  color: red;
}

.success-message {
  position: relative;
  font-size: 20px;
  color: green;
}

.founds-nothing {
  position: absolute;
  top: 60%;
  left: 50%;
  font-size: 30px;
  color: rgb(14, 89, 119);
}

.mini-card-textfield {
  margin: 5px;
  position: relative;
  top: -60px;
  font-size: 19px;
}

.colors-textfield {
  display: flex;
}

.mini-card-color{
  margin-left: 4px;
  width: 17px;
  height: 17px;
}

.form-btn {
  margin: auto;
  width: 155px;
  height: 40px;
  font-size: 20px;
  background: rgb(177, 247, 243);
  border: none;
  box-shadow: 2px 2px 13px grey;
  border-radius: 10px;
  transition: all 0.4s;
  cursor: pointer;
}

.form-btn:hover {
  transition: all 0.4s;
  transform: scale(1.1);

}

.mini-cards-container {
  width: 100%;
  height: 100vh;
  background: rgb(233, 247, 248);
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.mini-card {
  width: 10%;
  min-width: 200px;
  height: 30%;
  margin: 20px;
  padding: 10px;
  background: rgb(187, 252, 187);
  box-shadow: 2px 2px 10px grey;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mini-card-title {
  position: relative;
  left: -38px;
  top: -95px;
}

.mini-card-image {
  width: 100px;
  height: 100px;
  margin-top: -6px;
  margin-left: 116px;
}

@media (max-width: 1140px) {
  .form {
    flex-direction: column;
  }
}

/*Loading*/
.circle {
  position: absolute;
  top: 40%;
  left: 50%;
  width: 130px;
  height: 130px;
  border-radius: 50%;
  border: 10px solid rgb(41, 41, 41);
  border-top: 10px solid #fff;
  animation: anim 2s infinite linear;
}

@keyframes anim {
  0% {
    transform: translate(-20%, 50%) rotate(0deg);
  }
  100% {
    transform: translate(-20%, 50%) rotate(360deg);
  }
}

/*Popup card*/
.popup {
  position: fixed;
  z-index: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  overflow-y: auto;
}

.popup-body {
  position: relative;
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 10px;
}

.popup-content {
  position: relative;
  z-index: 2;
  background-color: #fff;
  color: black;
  max-width: 800px;
  padding: 30px;
}

.popup-title {
  text-align: center;
  font-size: 36px;
  margin: 0 0 1em 0;
}

.popup-close {
  position: absolute;
  z-index: 1;
  right: 0;
  top: 0;
  padding: 20px;
  font-size: 30px;
  text-decoration: none;
  color: rgb(255, 254, 254);
}

.popup-img {
  position: relative;
  width: 100%;
}

.popup-subtitle {
  margin: 0;
  font-size: 18px;
}

.home-btn {
  font-size: 40px;
  margin-left: 20px;
  color: black;
  position: absolute;
  left: 0;
}

.photo-page-img {
  text-decoration: none;
  width: 100%;
}

.photo-page-title {
  position: absolute;
  top: 100px;
  left: 50%;
  color: white;
  font-size: 30px;
}
